import React, { Component, useContext, useEffect, useState } from 'react';
import Axios from 'axios';
import { AuthContext } from '../../../App';
import { Redirect } from 'react-router';
export default function EditVendor() {
    const {state} = useContext(AuthContext)

    if (state.isAuthenticated === false) {
        alert('Login First')
        return <Redirect to="/login" />
    }
    const [Name, setName] = useState([]);
    const [Product_qty, setProduct_qty] = useState([]);
    const [Product_Price, SetProduct_Price] = useState([]);
    const [Product_Image, setProduct_Image] = useState([]);
    const [Discount, setDiscount] = useState([]);
    const [Vendor_ID, setVendor_ID] = useState([]);
    const [Expiry_Date, setExpiry_Date] = useState([]);
    const [product_id, setProduct_id] = useState([]);
    const [Category, setCategory] = useState([]);

    function editProducts() {
        Axios.patch("/admin/products", {
            
                Vendor_ID: Vendor_ID,
                max_product_qty: Product_qty,
                product_name: Name,
                product_price: Product_Price,
                product_image: Product_Image,
                discount: Discount,
                category: Category,
                expiry_date: Expiry_Date,
                Product_ID: product_id
        }, { headers: { Authorization: localStorage.getItem('token') } })
            .then((res) => {
                alert(JSON.stringify(res.data))
            })
            .catch((err) => {
                alert(err);
            })
    }

    return (
        <div id="layoutSidenav_content">
            <main>
                <div className="col-xl-12 col-md-12">
                    <div className="card card-static-2 mb-30">
                        <div className="card-title-2">
                            <h4>Edit Product</h4>
                        </div>
                        <div className="container">
                            <div className="Row">
                                <form className="p-5">
                                    <div className="form-group">
                                        <label >Product ID you want to edit</label>
                                        <input type="text" onChange={(e) => setProduct_id(e.target.value)} className="form-control" placeholder="Enter product name" />
                                    </div>
                                    <div className="form-group">
                                        <label >Name</label>
                                        <input type="email" onChange={(e) => setName(e.target.value)} className="form-control" placeholder="Enter product name" />
                                    </div>
                                    <div className="form-group">
                                        <label >Quantity</label>
                                        <input type="Number" onChange={(e) => setProduct_qty(e.target.value)} className="form-control" placeholder="Enter product quantity" />
                                    </div>
                                    <div className="form-group">
                                        <label >Price</label>
                                        <input type="text" onChange={(e) => SetProduct_Price(e.target.value)} className="form-control" placeholder="Enter Product price" />
                                    </div>
                                    <div className="form-group">
                                        <label >Image</label>
                                        <input type="text" onChange={(e) => setProduct_Image(e.target.value)} className="form-control" placeholder="Enter image URL" />
                                    </div>
                                    <div className="form-group">
                                        <label >Discount</label>
                                        <input type="number" onChange={(e) => setDiscount(e.target.value)} className="form-control" placeholder="Enter email" />
                                    </div>
                                    <div className="form-group">
                                        <label >Vendor ID</label>
                                        <input type="Number" onChange={(e) => setVendor_ID(e.target.value)} className="form-control" placeholder="Password" />
                                    </div>
                                    <div className="form-group">
                                        <label >Expiry Date</label>
                                        <input type="date" onChange={(e) => setExpiry_Date(e.target.value)} className="form-control" placeholder="Password" />
                                    </div>
                                    <div className="form-group">
                                        <label >Category</label>
                                        <input type='text' onChange={(e) => setCategory(e.target.value)} className="form-control" placeholder="Category" />
                                    </div>
                                    <button onClick={editProducts} className="btn btn-primary">Submit</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>

    );
}

