import React, { useCallback, useContext, useEffect, useState } from 'react';
import Axios from 'axios';
import { Redirect, useHistory } from 'react-router';
import { AuthContext } from '../../../App';
export default function Home(props) {
    const { state } = useContext(AuthContext)
    const history = useHistory();
    const OrderEdit = useCallback(() => history.push("/order/edit"), [history]);

    if (state.isAuthenticated === false) {
        return <Redirect to="/login" />
    }
    const [orders, setOrder] = useState([]);
    const [Canceled, setCanceled] = useState(0);
    const [Pending, setPending] = useState(0);
    const [Process, setProcess] = useState(0);
    const [Dispatched, SetDispatched] = useState(0);
    const [Income, setIncome] = useState(0);
    const [Delivered, SetDelivered] = useState(0);


    const getOrders = () => {
        Axios.get("/admin/orders", { headers: { Authorization: localStorage.getItem('token') } })
            .then((res) => {
                const par = JSON.stringify(res.data);
                setOrder(res.data.orders)
                console.log(res.data.data);
                res.data.data.forEach((val) => {
                    if (val.status == 'Pending') {
                        setPending(val.c)
                    } else if (val.status == 'Canceled') {
                        setCanceled(val.c)
                    } else if (val.status == 'Process') {
                        setProcess(val.c)
                    } else if (val.status == 'Delivered') {
                        SetDelivered(val.c)
                    } else if (val.status == 'Dispatched') {
                        SetDispatched(val.c)
                    }
                })
            })
            .catch((err) => {
                console.log(err);
            })
    }
    useEffect(() => {
        getOrders()
    }, [])
    return (
        <div id="layoutSidenav_content">
            <main>
                <div className="container-fluid">
                    <h2 className="mt-30 page-title">Dashboard</h2>
                    <ol className="breadcrumb mb-30">
                        <li className="breadcrumb-item active">Dashboard</li>
                    </ol>
                    <div className="row">
                        <div className="col-xl-4 col-md-6">
                            <div className="dashboard-report-card purple">
                                <div className="card-content">
                                    <span className="card-title">Order Pending</span>
                                    <span className="card-count">{Pending}</span>
                                </div>
                                <div className="card-media">
                                    <i className="fab fa-rev" />
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-md-6">
                            <div className="dashboard-report-card red">
                                <div className="card-content">
                                    <span className="card-title">Order Cancel</span>
                                    <span className="card-count">{Canceled}</span>
                                </div>
                                <div className="card-media">
                                    <i className="far fa-times-circle" />
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-md-6">
                            <div className="dashboard-report-card info">
                                <div className="card-content">
                                    <span className="card-title">Order Process</span>
                                    <span className="card-count">{Process}</span>
                                </div>
                                <div className="card-media">
                                    <i className="fas fa-sync-alt rpt_icon" />
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-md-6">
                            <div className="dashboard-report-card info">
                                <div className="card-content">
                                    <span className="card-title">Dispatched</span>
                                    <span className="card-count">{Dispatched}</span>
                                </div>
                                <div className="card-media">
                                    <i className="fas fa-money-bill rpt_icon" />
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-md-6">
                            <div className="dashboard-report-card success">
                                <div className="card-content">
                                    <span className="card-title">Today Income</span>
                                    <span className="card-count">&#8377; {Income}</span>
                                </div>
                                <div className="card-media">
                                    <i className="fas fa-money-bill rpt_icon" />
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-md-6">
                            <div className="dashboard-report-card success">
                                <div className="card-content">
                                    <span className="card-title">Delivered</span>
                                    <span className="card-count">{Delivered}</span>
                                </div>
                                <div className="card-media">
                                    <i className="fas fa-money-bill rpt_icon" />
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-12 col-md-12">
                            <div className="card card-static-2 mb-30">
                                <div className="card-title-2">
                                    <h4>Recent Orders</h4>
                                    <a onClick={OrderEdit} className="view-btn hover-btn">
                                        <i className="fas fa-edit " />
                                    </a>
                                </div>
                                <div className="card-body-table">
                                    <div className="table-responsive">
                                        <table className="table ucp-table table-hover">
                                            <thead>
                                                <tr>
                                                    <th style={{ width: 150 }}>Order ID</th>
                                                    <th style={{ width: 100 }}>Customer ID</th>
                                                    <th style={{ width: 100 }}>Product ID</th>
                                                    <th style={{ width: 100 }}>Product Q.</th>
                                                    <th style={{ width: 200 }}>Status</th>
                                                    <th style={{ width: 200 }}>Delivery Date</th>
                                                    <th style={{ width: 200 }}>Order Date</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {orders.map((order) => (
                                                    <tr key={order.orderID + " " + order.Product_ID}>
                                                        <td>{order.orderID}</td>
                                                        <td>{order.User_ID}</td>
                                                        <td>
                                                            {order.Product_ID}
                                                        </td>
                                                        <td>
                                                            {order.product_qty}
                                                        </td>
                                                        <td>
                                                            {order.status}
                                                        </td>
                                                        <td>
                                                            {order.delivery_date}
                                                        </td>
                                                        <td>&#8377;{order.order_date}</td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
            <footer className="py-4 bg-footer mt-auto">
                <div className="container-fluid">
                    <div className="d-flex align-items-center justify-content-between small">
                        <div className="text-muted-1"><b>EagleAI Dashboard</b>. by <a href="https://github.com/ashu-dixit">Ashutosh</a></div>
                    </div>
                </div>
            </footer>
        </div>

    );
}

