import React, { Component, useCallback, useContext } from 'react';
import { Redirect, useHistory } from 'react-router';
import { AuthContext } from '../../../App'
export default function Header() {
    const history = useHistory();
    const { dispatch } = useContext(AuthContext)
    const HomeOnClick = useCallback(() => history.push("/"), [history]);
    const LoginOnClick = useCallback(() => history.push("/login"), [history]);

    const HandleLogout = () => {
        dispatch({
            type: 'LOGOUT'
        })
    }
    return (
        <div>
            <nav className="sb-topnav navbar navbar-expand navbar-light bg-clr">
                <a className="navbar-brand logo-brand" >Big Supermarket</a>
                <button className="btn btn-link btn-sm order-1 order-lg-0" id="sidebarToggle" ><i className="fas fa-bars" /></button>
                <a onClick={HomeOnClick} className="frnt-link"><i className="fas fa-external-link-alt" />Home</a>
                <ul className="navbar-nav ml-auto mr-md-0">
                    <li className="nav-item dropdown">
                        <a className="nav-link dropdown-toggle" id="userDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"><i className="fas fa-user fa-fw" /></a>
                        <div className="dropdown-menu dropdown-menu-right" aria-labelledby="userDropdown">
                            <span onClick={HandleLogout} className="dropdown-item admin-dropdown-item">Logout</span>
                            <span onClick={LoginOnClick} className="dropdown-item admin-dropdown-item">Login</span>

                        </div>
                    </li>
                </ul>
            </nav>

        </div>
    );
};
