import React, { Component, useCallback, useEffect, useState } from 'react';
import Axios from 'axios';
import { useHistory } from 'react-router';
export default function Vendor() {
    const history = useHistory();
    const VendorEdit = useCallback(() => history.push("/Vendor/edit"), [history]);

    const [Vendors, setVendors] = useState([]);
    const getVendor = () => {
        Axios.get("/admin/vendors", { headers: { Authorization: localStorage.getItem('token') } })
            .then((res) => {
                const par = JSON.stringify(res.data);
                setVendors(res.data)
            })
            .catch((err) => {
                console.log(err);
            })
    }
    useEffect(() => {
        getVendor()
    }, [])
    return (
        <div id="layoutSidenav_content">
            <main>
                <div className="col-xl-12 col-md-12">
                    <div className="card card-static-2 mb-30">
                        <div className="card-title-2">
                            <h4>All Vendor</h4>
                            <a onClick={VendorEdit} className="view-btn hover-btn">View All</a>
                        </div>
                        <div className="card-body-table">
                            <div className="table-responsive">
                                <table className="table ucp-table table-hover">
                                    <thead>
                                        <tr>
                                            <th style={{ width: 120 }}>Vendor ID</th>
                                            <th style={{ width: 80 }}>Name</th>
                                            <th style={{ width: 100 }}>Mobile No.</th>
                                            <th style={{ width: 150 }}>Shop Owner name</th>
                                            <th style={{ width: 150 }}>Shop GST No.</th>
                                            <th style={{ width: 150 }}>Shop Phone No.</th>
                                            <th style={{ width: 130 }}>Shop Name</th>
                                            <th style={{ width: 130 }}>Location</th>
                                            <th style={{ width: 100 }}>Action</th>

                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Vendors.map((vendor) => (
                                            <tr >
                                                <td>{vendor.User_ID}</td>
                                                <td>{vendor.Name} </td>
                                                <td>{vendor.MobNo1}</td>
                                                <td>{vendor.Shop_Owner_name}</td>
                                                <td>{vendor.ShopGstno}</td>
                                                <td>{vendor.ShopPhoneno}</td>
                                                <td>{vendor.Shop_name}</td>
                                                <td>{vendor.latitudes}, {vendor.longitude}</td>

                                                <td className="action-btns" >
                                                    <a onClick={() => { console.log("He"); }}>
                                                        <i className="fas fa-edit" />
                                                    </a>
                                                </td>
                                            </tr>
                                        ))}

                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </main>

        </div>

    );
}

