import React, { useContext, useState } from 'react';
import Axios from 'axios';
import { Redirect } from 'react-router';
import { AuthContext } from '../../../App';
export default function EditOrder() {
    const {state} = useContext(AuthContext)

    if (state.isAuthenticated === false) {
        alert('Login First')
        return <Redirect to="/login" />
    }
    const [OrderId, setOrder_ID] = useState("");
    const [status, setStatus] = useState("");
    const [delivery_date, SetDelivery_Date] = useState("");
    const [Product_ID, setProduct_ID] = useState("");


    function editOrder() {
        Axios.patch("/admin/orders", {
                OrderId, status, delivery_date, Product_ID
            },{ headers: { Authorization: localStorage.getItem('token') } }
        )
        .then((res) => {
            alert(res.data)
            console.log(res)
        })
        .catch((err) => {
            alert(err);
            console.log(err)
        })
    }

    return (
        <div id="layoutSidenav_content">
            <main>
                <div className="col-xl-12 col-md-12">
                    <div className="card card-static-2 mb-30">
                        <div className="card-title-2">
                            <h4>Edit Order</h4>
                        </div>
                        <div className="container">
                            <div className="Row">
                                    <div className="form-group">
                                        <label >Order_ID</label>
                                        <input type="text" onChange={(e) => setOrder_ID(e.target.value)} className="form-control" placeholder="Enter Order ID" />
                                    </div>
                                    <div className="form-group">
                                        <label >Product ID</label>
                                        <input type="text" onChange={(e) => setProduct_ID(e.target.value)} className="form-control" placeholder="Enter Product ID" />
                                    </div>
                                    <div className="form-group">
                                        <label >Status</label>
                                        <input type="text" onChange={(e) => setStatus(e.target.value)} className="form-control" placeholder="Enter Status" />
                                    </div>
                                    <div className="form-group">
                                        <label >Delivery Date</label>
                                        <input type="date" onChange={(e) => SetDelivery_Date(e.target.value)} className="form-control" />
                                    </div>
                                    <button onClick={editOrder} className="btn btn-primary">Submit</button>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>

    );
}

