import React, { Fragment, useContext, useState } from 'react'
import axios from 'axios'
import { AuthContext } from '../App'
const qs = require('querystring')
const api = 'http://localhost:3001'

var Recaptcha = require('react-recaptcha');

function LoginComp(props) {
    const {state} = useContext(AuthContext)

    if(state.isAuthenticated){
        return <h1>Already</h1>
    }

    const { dispatch } = useContext(AuthContext)

    const initialState = {
        isSubmitting: false,
        errorMessage: null,
    }

    const stateForm = {
        mobile: "",
        password: ""
    }

    const [data, setData] = useState(initialState)
    const [dataform, setDataForm] = useState(stateForm)

    const handleInputChange = event => {
        setDataForm({
            ...dataform,
            [event.target.name]: event.target.value,
        })
        console.log(dataform);
    }

    const handleFormSubmit = event => {
        event.preventDefault()

        // if (data.isVerified) {
        setData({
            ...data,
            isSubmitting: true,
            errorMessage: null
        })

        const requestBody = {
            MobNo1: dataform.mobile,
            password: dataform.password
        }

        const config = {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        }

        axios.post('/auth/admin', qs.stringify(requestBody), config)
            .then(res => {
                console.log(res);
                if (res.data.success === true && res.data.isVerified === 1) {
                    dispatch({
                        type: "LOGIN",
                        payload: res.data
                    })

                }
                else if (res.data.success === true && res.data.isVerified === 0) {
                    setData({
                        ...data,
                        isSubmitting: false,
                        errorMessage: res.data.message
                    })
                }
                else {
                    setData({
                        ...data,
                        isSubmitting: false,
                        errorMessage: res.data.message
                    })
                }

                throw res
            })
            .catch(e => {
                console.log(e)
            })
        // }
        // else {
        //     alert('Anda diduga robot!')
        // }
    }

    return (
        <div id="layoutSidenav_content">
            <div className="container">
                <br />
                <div className="row">
                    <div className="col">
                        <h1>Login Form</h1>

                        <hr />

                        <form onSubmit={handleFormSubmit}>
                            <div className="form-group">
                                <label >Mobile Number</label>
                                <input
                                    className="form-control"
                                    type="text"
                                    onChange={handleInputChange}
                                    name="mobile"
                                    placeholder="Enter Mobile Number"
                                    value={dataform.mobile}
                                />
                            </div>
                            <div className="form-group">
                                <label >Password</label>
                                <input
                                    className="form-control"
                                    type="password"
                                    onChange={handleInputChange}
                                    name="password"
                                    id="examplePassword"
                                    placeholder="Enter Your Password"
                                    value={dataform.password}
                                />
                            </div>
                            {/* <div className="form-group">
                                <Recaptcha
                                    onloadCallback={callback}
                                    sitekey="6LfQabkZAAAAAK-AjTDGDIB4VTm0RSEtG0XjmePe"
                                    render="explicit"
                                    verifyCallback={verifyCallback}
                                />
                            </div> */}
                            <div className="form-group">
                                {data.errorMessage && (
                                    <div className="alert alert-danger" role="alert">
                                        {data.errorMessage}
                                    </div>
                                )}
                            </div>
                            <div className="form-group">
                                <button className="btn btn-primary" disabled={data.isSubmitting}>
                                    {data.isSubmitting ? (
                                        "..Loading"
                                    ) :
                                        (
                                            "Login"
                                        )
                                    }
                                </button>
                            </div>
                        </form>
                    </div>
                </div >
            </div>
        </div>
    )
}



export default LoginComp