import React, { createContext, useEffect, useReducer } from 'react';
import rootRoutes from './components/admin/rootRoutes';
import NoMatch from './components/nomatch';
import { Switch, Route, MemoryRouter, Redirect } from 'react-router-dom';
import Header from './components/admin/header';
import Home from './components/admin/dashboard';
import SideBar from './components/admin/sidebar';
import Product from './components/admin/product';
import Customer from './components/admin/customer'
import Vendor from './components/admin/vendor'
import AddProduct from './components/admin/product/AddProduct';
import EditProduct from './components/admin/product/EditProduct';
import EditCustomer from './components/admin/customer/EditCustomer';
import EditVendor from './components/admin/vendor/EditVendor';
import EditOrder from './components/admin/dashboard/editorder';
import LoginComp from './components/Login';

// import PrivateRoute from './components/auth/PrivateRoute';
// import "react-notifications/lib/notifications.css";

export const AuthContext = createContext()

//Inisiasi state
const initialState = {
	isAuthenticated: null,
	user: null,
	token: null,
	tokenExpires: 0,
	role: 0
}

const reducer = (state, action) => {
	switch (action.type) {
		case "LOGIN":
			localStorage.setItem("user", JSON.stringify(action.payload.user))
			localStorage.setItem("token", action.payload.token)
			return {
				...state,
				isAuthenticated: true,
				user: action.payload.user,
				token: action.payload.token
			}
		case "LOGOUT":
			localStorage.clear()
			return {
				...state,
				isAuthenticated: false,
				user: null
			}
		default:
			return state
	}
}


export default function App() {
	const [state, dispatch] = useReducer(reducer, initialState)
	if(!state.isAuthenticated && localStorage.length != 0){
		dispatch({
			type: 'LOGIN',
			payload:{
				user: localStorage.getItem('user'),
				token: localStorage.getItem('token')
			}
		})
	}
	return (
		<div className="App">
			<MemoryRouter>
				<Switch>
					<AuthContext.Provider value={{
						state,
						dispatch
					}}>
						<Header />
						<div id="layoutSidenav">
							<SideBar />
							<Route exact path='/' component={Home} />
							<Route path='/customer/list' component={Customer} />
							<Route path='/vendor/list' component={Vendor} />
							<Route path='/product/list' component={Product} />
							<Route path='/product/create' component={AddProduct} />
							<Route path='/product/edit' component={EditProduct} />
							<Route path='/customer/edit' component={EditCustomer} />
							<Route path='/Vendor/edit' component={EditVendor} />
							<Route path='/order/edit' component={EditOrder} />
							<Route path='/login' component={LoginComp} />
						</div>
					</AuthContext.Provider>
				</Switch>
			</MemoryRouter>
		</div>
	);
}

