import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom'
import Header from './header';
import Home from './dashboard';
import SideBar from './sidebar';
import Product from './product';
import Customer from './customer'
import Vendor from './vendor'
import AddProduct from './product/AddProduct';
import EditProduct from './product/EditProduct';
import EditCustomer from './customer/EditCustomer';
import EditVendor from './vendor/EditVendor';
import EditOrder from './dashboard/editorder';
import LoginComp from '../Login';


export default class rootRoutes extends Component {
  render() {
    const { match } = this.props;
    return (
      <main>
        <Header />
        <div id="layoutSidenav">
          <SideBar />
          <Switch>
            <Route exact path={[`${match.path}/home`, `${match.path}`]} component={Home} />
            <Route path='/customer/list' component={Customer} />
            <Route path='/vendor/list' component={Vendor} />
            <Route path='/product/list' component={Product} />
            <Route path='/product/create' component={AddProduct} />
            <Route path='/product/edit' component={EditProduct} />
            <Route path='/customer/edit' component={EditCustomer} />
            <Route path='/Vendor/edit' component={EditVendor} />
            <Route path='/order/edit' component={EditOrder} />
            <Route path='/login' component={LoginComp} />
          </Switch>
        </div>
      </main>
    );
  }
}