import React, { useContext, useEffect, useState } from 'react';
import Axios from 'axios';
import { AuthContext } from '../../../App';
import { Redirect } from 'react-router';
export default function Product() {
    const { state } = useContext(AuthContext)

    if (state.isAuthenticated === false) {
        alert('Login First')
        return <Redirect to="/login" />
    }
    const [Products, setProducts] = useState([]);
    const [ProductToDelete, setProductToDelete] = useState(null);

    const getProducts = () => {
        Axios.get("/admin/products", { headers: { Authorization: localStorage.getItem('token') } })
            .then((res) => {
                const par = JSON.stringify(res.data);
                setProducts(res.data)
            })
            .catch((err) => {
                console.log(err);
            })
    }
    const deleteProduct = () => {
        Axios.delete('/admin/products', { headers: { Authorization: state.token }, data: { Product_ID: ProductToDelete } })
            .then((res) => {
                alert('Product ' + ProductToDelete + ' Disabled')
            })
            .catch((err) => {
                alert('Error occured while deleting')
            })
    }
    useEffect(() => {
        getProducts(1);
    }, [])

    return (
        <div id="layoutSidenav_content">
            <main>
                <div className="col-xl-12 col-md-12">
                    <div className="card card-static-2 mb-30">
                        <div className="card-title-2">
                            <h4>Recent Orders</h4>
                            <a href="#myModal" className="view-btn hover-btn" data-toggle="modal">
                                <i className="fas fa-trash-alt " />
                            </a>
                            <div id="myModal" className="modal fade">
                                <div className="modal-dialog modal-confirm">
                                    <div className="modal-content">
                                        <div className="modal-header flex-column">
                                            <div className="icon-box">
                                                <i className="fas fa-times"></i>
                                            </div>
                                            <h4 className="modal-title w-100">Are you sure?</h4>
                                            <button type="button" className="close" data-dismiss="modal" aria-hidden="true">&times;</button>
                                        </div>
                                        <form>
                                            <div className="form-group row">
                                                <label className="col-sm-4 col-form-label">Product ID</label>
                                                <div className="col-sm-8">
                                                    <input type="number" className="form-control" onChange={e => setProductToDelete(e.target.value)} placeholder="Product ID" />
                                                </div>
                                            </div>
                                            <button type="button" className="btn btn-danger" data-dismiss="modal" onClick={() => deleteProduct()}>Delete</button>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="card-body-table">
                            <div className="table-responsive">
                                <table className="table ucp-table table-hover">
                                    <thead>
                                        <tr>
                                            <th style={{ width: 130 }}>Product ID</th>
                                            <th style={{ width: 130 }}>Product Name</th>
                                            <th style={{ width: 200 }}>Product Qty.</th>
                                            <th style={{ width: 200 }}>Product Price</th>
                                            <th style={{ width: 130 }}>Product Image</th>
                                            <th style={{ width: 130 }}>Discount</th>
                                            <th style={{ width: 100 }}>Vendor ID</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Products.map((product) => (
                                            <tr key={product.Product_ID}>
                                                <td>{product.Product_ID}</td>
                                                <td>{product.product_name} </td>
                                                <td>{product.max_product_qty}</td>
                                                <td>&#8377;{product.product_price}</td>
                                                <td><a href="#" className="avatar avatar-sm">
                                                    <img alt="Image not available" height="30px" src={product.product_image} /></a></td>
                                                <td>{product.discount} %</td>
                                                <td>{product.Vendor_ID}</td>
                                            </tr>
                                        ))}

                                    </tbody>
                                </table>

                            </div>
                        </div>
                    </div>
                </div>
            </main>

        </div>

    );
}

