import React, { Component, useCallback, useEffect, useState } from 'react';
import Axios from 'axios';
import { useHistory } from 'react-router';
export default function EditVendor() {
    const history = useHistory();
    const VendorList = useCallback(() => history.push("/vendor/list"), [history]);

    const [Name, setName] = useState([]);
    const [MobNo1, setMobNo1] = useState([]);
    const [MobNo2, SetMobNo2] = useState([]);
    const [Address, setAddress] = useState([]);
    const [VERIFIED, setVERIFIED] = useState([]);
    const [City, setCity] = useState([]);
    const [deposit, setdeposit] = useState([]);
    const [Shop_Owner_name, setShop_Owner_name] = useState([]);
    const [ShopGstno, setShopGstno] = useState([]);
    const [ShopPhoneno, setShopPhoneno] = useState([]);
    const [Shop_name, setShop_name] = useState([]);
    const [latitudes, setlatitudes] = useState([]);
    const [longitude, setlongtitude] = useState([]);
    const [User_ID, setUser_ID] = useState([]);



    function editProducts() {
        Axios.patch("/admin/vendor", {
                Name, MobNo1, MobNo2, Address, VERIFIED, City, deposit,
                Shop_Owner_name, ShopGstno, ShopPhoneno, Shop_name, latitudes,
                longitude, User_ID
        }, { headers: { Authorization: localStorage.getItem('token') } })
            .then((res) => {
                console.log(res);
            })
            .catch((err) => {
                console.log(err);
            })
    }

    return (
        <div id="layoutSidenav_content">
            <main>
                <div className="col-xl-12 col-md-12">
                    <div className="card card-static-2 mb-30">
                        <div className="card-title-2">
                            <h4>Edit Vendor</h4>
                            <a onClick={VendorList} className="view-btn hover-btn">Go Back</a>
                        </div>
                        <div className="container">
                            <div className="Row">
                                <form className="p-5">
                                    <div className="form-group">
                                        <label >Name</label>
                                        <input type="email" onChange={(e) => setName(e.target.value)} className="form-control" placeholder="Enter Vendor name" />
                                    </div>
                                    <div className="form-group">
                                        <label >Mobile Number</label>
                                        <input type="text" onChange={(e) => setMobNo1(e.target.value)} className="form-control" placeholder="Enter Mobile Number" />
                                    </div>
                                    <div className="form-group">
                                        <label >Alt. Mobile Number</label>
                                        <input type="text" onChange={(e) => SetMobNo2(e.target.value)} className="form-control" placeholder="Enter Second Mobile Number" />
                                    </div>
                                    <div className="form-group">
                                        <label >Address</label>
                                        <input type="text" onChange={(e) => setAddress(e.target.value)} className="form-control" placeholder="Enter Address" />
                                    </div>
                                    <div className="form-group">
                                        <label >City</label>
                                        <input type="text" onChange={(e) => setCity(e.target.value)} className="form-control" placeholder="Enter City" />
                                    </div>
                                    <div className="form-group">
                                        <label >Shop Owner name</label>
                                        <input type="text" onChange={(e) => setShop_Owner_name(e.target.value)} className="form-control" placeholder="Enter shop owner name" />
                                    </div>
                                    <div className="form-group">
                                        <label >Shop GST Number</label>
                                        <input type="text" onChange={(e) => setShopGstno(e.target.value)} className="form-control" placeholder="Enter Shop GST Number" />
                                    </div>
                                    <div className="form-group">
                                        <label >Shop Phone Number</label>
                                        <input type="text" onChange={(e) => setShopPhoneno(e.target.value)} className="form-control" placeholder="Enter Shop Phone Number" />
                                    </div>
                                    <div className="form-group">
                                        <label >Shop Name</label>
                                        <input type="text" onChange={(e) => setShop_name(e.target.value)} className="form-control" placeholder="Enter Shop Name" />
                                    </div>
                                    <div className="form-group">
                                        <label >Latitude</label>
                                        <input type="text" onChange={(e) => setlatitudes(e.target.value)} className="form-control" placeholder="Enter Latitude" />
                                    </div>
                                    <div className="form-group">
                                        <label >Longitude</label>
                                        <input type="text" onChange={(e) => setlongtitude(e.target.value)} className="form-control" placeholder="Enter Longitude" />
                                    </div>
                                    <div className="form-group">
                                        <label >Deposit</label>
                                        <input type="text" onChange={(e) => setdeposit(e.target.value)} className="form-control" placeholder="Enter Longitude" />
                                    </div>
                                    <div className="form-group">
                                        <label >User ID you want to edit</label>
                                        <input type="Number" onChange={(e) => setUser_ID(e.target.value)} className="form-control" placeholder="Enter Unique User ID" />
                                    </div>
                                    <div className="form-group">
                                        <label >VERIFIED</label>
                                        <input type="number" onChange={(e) => setVERIFIED(e.target.value)} className="form-control" placeholder="Enter 0 or 1 for true or false" />
                                    </div>
                                    <button onClick={editProducts} className="btn btn-primary">Submit</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>

    );
}

